<template>
  <div class="detail">
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <el-form :model="form" label-position="left" :rules="rules" ref="form" label-width="80px" class="form">
        <el-form-item class="item" label="姓名" prop="name">
          <el-input class="input" v-model="form.name" placeholder="请输入姓名">
            <el-select class="sex-select" v-model="form.sex" slot="append" placeholder="性别">
              <el-option v-for="item in $enums.Sex.list" :label="item.label" :value="item.value"
                         :key="item.value"></el-option>
            </el-select>
          </el-input>
        </el-form-item>
        <el-form-item class="item" label="是否教师" prop="isTeacher">
          <el-radio-group v-model="form.isTeacher">
            <el-radio :key="key" v-for="(item,key) in $enums.YesNoStatus.list" :label="item.value" border>&nbsp;&nbsp;{{item.label}}&nbsp;&nbsp;</el-radio>
          </el-radio-group>
        </el-form-item>
        <!--<el-form-item class="item" label="性别" prop="sex">-->
        <!--<el-radio-group >-->
        <!--<el-radio :key="key" v-for="(item,key) in $enums.Sex.list" :label="item.value" border>&nbsp;&nbsp;{{item.label}}&nbsp;&nbsp;</el-radio>-->
        <!--</el-radio-group>-->
        <!--</el-form-item>-->
        <el-form-item class="item" label="手机号" prop="phone">
          <el-input class="input phone"  v-model="form.phone" maxLength="11" type="number" placeholder="请输入手机号"></el-input>
        </el-form-item>


        <el-form-item class="item" label="所属部门" prop="orgIds">
          <el-input disabled class="input dep" v-model="selectedDepName.join()" placeholder="请选择所属部门">
            <el-button @click="depDialog = true" class="hand" slot="append">选择部门</el-button>
          </el-input>
        </el-form-item>
        <el-form-item class="item" label="职务">
          <el-input class="input" v-model="form.position" placeholder="请输入职务"></el-input>
        </el-form-item>
        <el-form-item class="item" label="角色" prop="roleIds">
          <el-select v-model='roleIds' class="input">
            <el-option v-for="(item,key) in roleList"
                       :disabled="item.id === 1"
                       :label="item.name" :value="item.id" :key="key"
            ></el-option>
          </el-select>
        </el-form-item>

      </el-form>
      <div class="btns">
        <div @click="doSubmit" class="btn-submit btn-add mr20">{{editStatus? '更新': '新增'}}</div>
        <div @click="$router.go(-1)" class="btn-clear">返回</div>
      </div>
      <el-dialog :visible.sync="depDialog" class="dialog" width="400px">
        <div class="dep-form">
          <p class="title">{{depTitle}}</p>
          <div class="content">
            <item :data="depData" :selectedDep.sync="selectedDep"
                  :selectedDepName.sync="selectedDepName"
            ></item>
          </div>
          <div class="btns">
            <div @click="depDialog = false" class="btn-submit">关闭</div>
          </div>
        </div>
      </el-dialog>
    </main-part>
  </div>

</template>
<script>
  import item from './Item'

  export default {
    components: {
      item
    },
    data () {
      var checkOrgIds = (rule, value, callback) => {
        if (!this.selectedDep.length) {
          return callback(new Error('请选择所属部门'))
        } else {
          callback()
        }
      }
      var checkRoleIds = (rule, value, callback) => {
        if (!this.roleIds) {
          return callback(new Error('请选择用户角色'))
        } else {
          callback()
        }
      }
      return {
        // 是否为编辑状态
        editStatus: this.$route.params.id !== '0',
        nav: [
          {name: '用户管理', path: 'go(-1)'},
          {name: this.editStatus ? '编辑用户' : '新增用户', path: ''}
        ],
        form: {
          name: '',
          sex: 'Male',
          phone: '',
          isTeacher: '',
          position: '',
          orgIds: '',
          roleIds: []
        },
        roleIds: '',
        rules: {
          name: {required: true, message: '请输入姓名', trigger: 'blur'},
          sex: {required: true, message: '请选择性别', trigger: 'change'},
          phone: [
            {required: true, message: '请输入手机号', trigger: 'blur'},
            {min: 11, max: 11, message: '手机号格式错误', trigger: 'blur'}
          ],
          isTeacher: {required: true, message: '请选择是否为教师', trigger: 'change'},
          orgIds: {required: true, validator: checkOrgIds, trigger: 'change'},
          // roleIds: {required: true, validator: checkRoleIds, trigger: 'change'},
          roleIds: {required: true, validator: checkRoleIds, trigger: 'change'},
        },
        //
        depTitle: '',
        // 部门树
        depData: [],
        // 选择的部门ID
        selectedDep: [],
        // 选择的部门名称
        selectedDepName: [],
        // 所有的角色
        roleList: [],
        // 选择部门弹窗
        depDialog: false
      }
    },
    methods: {
      // 获取页面内容
      async getDetail () {
        let res = await this.$ajax({
          url: '/user/getUserDetail.do',
          query: {
            userId: this.$route.params.id
          }
        })
        this.selectedDep = []
        res.orgList.forEach(e => {
          this.selectedDep.push(e.id)
          this.selectedDepName.push(e.name)
        })
        this.form = res
        // 角色多选
        // this.roleIds = res.roleList.map(e=> e.id)
        this.roleIds = res.roleList[0].id
      },
      // 提交
      async doSubmit () {
        let text = '用户新增成功'
        let url = '/user/addUser.do'
        if (this.editStatus) {
          text = '用户更新成功'
          url = '/user/updateUser.do'
        }
        await this.$refs.form.validate()
        this.form.orgIds = this.selectedDep
        this.form.roleIds = [this.roleIds]
        await this.$ajax({
          url,
          el: '.btn-add',
          noText: true,
          query: this.form
        })
        this.$remind(text)
        this.$router.push('/school/user')
      },
      // 更新
      async updateFun () {
        await this.$refs.form.validate()
        this.form.orgIds = this.selectedDep
        await this.$ajax({
          url: '/user/updateUser.do',
          el: '.btn-add',
          query: this.form
        })
        this.$remind('用户更新成功')
        this.$router.push('/school/user')
      },
      // 获取所有的部门
      async getAllDep () {
        let res = await this.$ajax({
          url: '/org/queryOrganizationTree.do',
          query: {
            orgTypes: ['ADMIN_ORG', 'TEACH_ORG']
          }
        })
        this.depTitle = res.orgName
        this.depData = this.recursion(res.subOrgList, 1)
      },
      // 递归给每一个item设置缩进和check-box隐藏
      recursion (arr, i) {
        arr.forEach(e => {
          e.tier = i
          e.status = false
          if (e.subOrgList.length > 0) {
            this.recursion(e.subOrgList, i + 1)
          }
        })
        return arr
      },
      // 获取所有角色
      async getAllRole () {
        this.roleList = await this.$ajax({url: '/role/queryRoleList.do'})

      },
    },
    mounted () {
      this.getAllDep()
      this.getAllRole()
      if (this.editStatus) {
        this.getDetail()
      }
    }
  }
</script>
