  .detail
    .dialog
      /deep/ .el-dialog
        border-radius: 10px
      /deep/ .el-dialog__body
        padding: 0
      /deep/ .el-dialog__header
        display: none
    .form
      margin-top: 20px
      width: 1000px
      @extend %clearfix
      .sex-select
        width: 75px
      .item
        float: left
        margin-right: 80px
        width: 420px
      .phone
        /deep/ input
          ime-mode: disabled
      .input
        width: 360px
        /deep/ .el-input-group__append
          background: #fff
      .dep /deep/ .el-input-group__append
        background: $main-c
        border-color: $main-c
        color: #fff
        cursor: pointer
        opacity: 1
        transition: all .3s
        &:hover
          opacity: .8
        &:active
          opacity: 1
    .dep-form
      border-radius: 10px
      padding-bottom: 20px
      .title
        line-height: 46px
        background: $main-c
        color: #fff
        padding-left: 15px
        border-radius: 10px 10px 0 0
      .content
        width: 100%
        height: 500px
        border-bottom: 1px solid $border
        overflow: auto
      .btns
        margin-left: 0
        margin-top: 20px
        text-align: center
    .btns
      margin-top: 20px
      margin-left: 80px
</style>
